del=\"nouveau.title\">\n\n                        <div class=\"list-group-option\" v-if=\"nouveau.type == 'choix' &&  nouveau.groupe.length != 0\">\n                            <p v-for=\"groupe in nouveau.groupe\">\n                                <input class=\"form-control\" v-model=\"groupe.text\" placeholder=\"Choix\">\n                            </p>\n                            <a class=\"btn btn-xs btn-info\" @click=\"addNewGroupe()\"><i class=\"fa fa-plus\"></i></a>\n                        </div>\n                    </div>\n                </div>\n                <p class=\"text-right margTop\"><a @click=\"ajouterOption\" class=\"btn btn-sm btn-primary\">Envoyer</a></p>\n            </li>\n        </ul>\n\n        <ul class=\"list-group\">\n            <li v-for=\"option in list\" :class=\"'list-group-item ' + option.type\">\n                <h4 class=\"list-option-title\">{{ option.type_name }}</h4>\n                <div class=\"row\">\n                    <div class=\"col-md-10\">\n                        <label><strong>Titre</strong></label>\n                        <p v-if=\"!option.state\">{{ option.title }}</p>\n                        <div v-if=\"option.state\">\n                            <input class=\"form-control\" name=\"title\" type=\"text\" v-model=\"option.title\">\n                        </div>\n                    </div>\n                    <div class=\"col-md-2 text-right\">\n                        <div class=\"btn-group\">\n                            <a v-show=\"!option.state\" @click=\"editOption(option)\" class=\"btn btn-xs btn-info\">éditer</a>\n                            <a v-show=\"!option.state\" @click=\"deleteOption(option)\" class=\"btn btn-xs btn-danger\"><i class=\"fa fa-times\"></i></a>\n                            <a v-show=\"option.state\" @click=\"saveOption(option)\" class=\"btn btn-xs btn-primary\">sauvegarder</a>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"row\" v-if=\"option.groupe.length != 0\">\n                    <div class=\"col-md-10\">\n                        <label><strong>Choix</strong></label>\n                        <p v-if=\"!option.state\" v-for=\"groupe in option.groupe\">{{ groupe.text }}</p>\n\n                        <div class=\"list-option-groupe\" v-if=\"option.state\">\n                            <p v-for=\"groupe in option.groupe\">\n                                <input class=\"form-control\" v-model=\"groupe.text\">\n                            </p>\n\n                            <div class=\"row\" v-if=\"newgroupitem\" style=\"margin-bottom:8px;\">\n                                <div class=\"col-md-11\">\n                                    <input class=\"form-control\" value=\"\" v-model=\"groupeitem.text\">\n                                </div>\n                                <div class=\"col-md-1\">\n                                    <button type=\"button\" @click=\"saveGroup(option)\" class=\"btn btn-sm btn-primary btn-input-h pull-right\">ajouter</button>\n                                </div>\n                            </div>\n\n                            <a class=\"btn btn-xs btn-info\" @click=\"addNewGroupeItem(option)\"><i class=\"fa fa-plus\"></i></a>\n                        </div>\n\n                    </div>\n                </div>\n            </li>\n        </ul>\n\n    </div>\n</template>\n<style>\n    #addOption{\n        margin-bottom:15px;\n    }\n    .margBottom{\n      padding-bottom:5px;\n    }\n    .margTop{\n        margin-top:10px;\n    }\n    .list-group-item {\n        padding: 8px 15px 8px 15px;\n    }\n    .list-group-item label{\n        margin-bottom: 5px;\n        margin-top: 5px;\n    }\n    .list-option-groupe p{\n           margin-bottom: 5px;\n    }\n    .list-group-option{\n        margin-top:10px;\n    }\n    .list-group-option p {\n        margin-bottom: 3px;\n    }\n    .option-title{\n        border-color:#6f7271;\n    }\n    .btn-input-h{\n        height:34px;\n    }\n</style>\n<script>\n\nexport default {\n\n    props: ['colloque','options'],\n    data () {\n        return {\n            list: [],\n            nouveau:{\n                title: '',\n                type: 'checkbox',\n                groupe: new Array(),\n                colloque_id: this.colloque,\n            },\n            groupeitem: {},\n            add : false,\n            newgroupitem: false,\n            isText: false\n        }\n    },\n    beforeMount: function () {\n        this.getOptions();\n    },\n    methods: {\n        getOptions : function(){\n             this.list = _.orderBy(this.options, ['type'],['desc']);\n        },\n        selectType : function(){\n\n            if(this.nouveau.type == 'choix')\n            {\n                 this.nouveau.groupe.push({ text: ''});\n            }\n            else{\n                this.nouveau.groupe = [];\n            }\n        },\n        editOption : function(option){\n            option.state = true;\n        },\n        ajouter : function(){\n            this.add = true;\n        },\n        addNewGroupe: function(option) {\n            this.nouveau.groupe.push({text: ''});\n        },\n        addNewGroupeItem: function(option) {\n            this.newgroupitem = true;\n            this.groupeitem.text = '';\n            this.groupeitem.option_id = option.id;\n            this.groupeitem.colloque_id = this.colloque;\n        },\n        resetform :function(){\n            this.add = false;\n            this.nouveau = {\n                title: '',\n                option: '',\n                type: 'checkbox',\n                groupe: [],\n                colloque_id: this.colloque,\n            };\n        },\n        ajouterOption:function(){\n\n            var self = this;\n            axios.post('/vue/option', { option : this.nouveau }).then(function (response) {\n                self.list = _.orderBy(response.data.options, ['type'],['desc']);\n                self.resetform();\n            }).catch(function (error) { console.log(error);});\n\n        },\n        saveOption : function(option){\n\n            var self = this;\n            axios.post('/vue/option/' + option.id, { option, '_method' : 'put' }).then(function (response) {\n               console.log(response.data.options);\n               self.list = _.orderBy(response.data.options, ['type'],['desc']);\n            }).catch(function (error) { console.log(error);});\n\n        },\n        saveGroup : function(option){\n\n            if(this.groupeitem.text.length){\n\n                var self = this;\n                axios.post('/vue/groupe', this.groupeitem).then(function (response) {\n                    self.list = _.orderBy(response.data.options, ['type'],['desc']);\n                }).catch(function (error) { console.log(error);});\n            }\n\n            this.newgroupitem = false;\n            this.groupeitem = {};\n        },\n        deleteOption :function(option){\n\n            var shouldDelete = false;\n\n            if(option.isUsed){\n                var answer = confirm('Cette option est lié à des inscription, voulez-vous vraiment supprimer?');\n                shouldDelete = answer ? true : false;\n            }\n            else{\n                shouldDelete = true;\n            }\n\n            if(shouldDelete){\n\n                var self = this;\n                axios.post('/vue/option/' + option.id, { '_method' : 'DELETE' }).then(function (response) {\n                    self.list = _.orderBy(response.data.options, ['type'],['desc']);\n                }).catch(function (error) { console.log(error);});\n            }\n        }\n    }\n}\n</script>"]}]);


