t {\n\n    props: ['occurrences','locations','colloque','prices'],\n    data () {\n        return {\n            list: [],\n            loc : [],\n            prix : [],\n            nouveau:{\n                title: '',\n                lieux_id: '',\n                starting_at: '',\n                capacite_salle: '',\n                colloque_id: this.colloque,\n                prices:[]\n            },\n            add : false,\n            errors:[]\n        }\n    },\n    beforeMount: function ()  {\n        this.getOccurrences();\n        this.getLocations();\n        this.getPrices();\n    },\n    methods: {\n        edit : function(occurence){\n            this.list[occurence.id].state = true;\n\n             this.$nextTick(function(){\n                 $.fn.datepicker.dates['fr'] = {\n                    days: ['Dimanche','Lundi','Mardi','Mercredi','Jeudi','Vendredi','Samedi'],\n                    daysShort: ['Dim','Lun','Mar','Mer','Jeu','Ven','Sam'],\n                    daysMin: ['Di','Lu','Ma','Me','Je','Ve','Sa'],\n                    months: ['Janvier','Février','Mars','Avril','Mai','Juin','Juillet','Août','Septembre','Octobre','Novembre','Décembre'],\n                    monthsShort: ['Jan','Fév','Mar','Avr','Mai','Jun','Jul','Aoû','Sep','Oct','Nov','Déc'],\n                    today: \"Aujourd'hui\",\n                    clear: \"Clear\"\n                };\n\n                $('.datePickerApp').datepicker({\n                    format: 'yyyy-mm-dd',\n                    language: 'fr'\n                }).on('changeDate', function(ev){\n                   occurence.starting_at = ev.target.value;\n                });\n\n            });\n        },\n        delete : function(occurence){\n            this.list[occurence.id].state = false;\n        },\n        getOccurrences : function(){\n\n           this.list = this.occurrences;\n\n           this.$nextTick(function(){\n\n               $.fn.datepicker.dates['fr'] = {\n                    days: ['Dimanche','Lundi','Mardi','Mercredi','Jeudi','Vendredi','Samedi'],\n                    daysShort: ['Dim','Lun','Mar','Mer','Jeu','Ven','Sam'],\n                    daysMin: ['Di','Lu','Ma','Me','Je','Ve','Sa'],\n                    months: ['Janvier','Février','Mars','Avril','Mai','Juin','Juillet','Août','Septembre','Octobre','Novembre','Décembre'],\n                    monthsShort: ['Jan','Fév','Mar','Avr','Mai','Jun','Jul','Aoû','Sep','Oct','Nov','Déc'],\n                    today: \"Aujourd'hui\",\n                    clear: \"Clear\"\n                };\n\n                var self = this;\n\n                $('.datePickerNew').datepicker({\n                    format: 'yyyy-mm-dd',\n                    language: 'fr'\n                }).on('changeDate', function(ev){\n                   self.nouveau.starting_at = ev.target.value;\n                });\n            });\n        },\n        getLocations : function(){\n           this.loc = this.locations;\n        },\n        getPrices : function(){\n           this.prix = this.prices;\n        },\n        updateOccurrences:function(occurrences){\n            this.list = occurrences;\n        },\n        checkForm:function(){\n\n            if( this.nouveau.title && this.nouveau.lieux_id && this.nouveau.starting_at && this.nouveau.capacite_salle) return true;\n            this.errors = [];\n            if(!this.nouveau.title) this.errors.push(\"Titre requis.\");\n            if(!this.nouveau.lieux_id) this.errors.push(\"Lieu requis.\");\n            if(!this.nouveau.starting_at) this.errors.push(\"Date requise.\");\n            if(!this.nouveau.capacite_salle) this.errors.push(\"Capacité requise.\");\n\n            return false;\n        },\n        ajouterOccurence:function(){\n\n            if(this.checkForm()){\n\n                var self = this;\n                axios.post('/vue/occurrence', { occurrence : this.nouveau }).then(function (response) {\n                     self.updateOccurrences(response.data.occurrences);\n                     self.resetform();\n                }).catch(function (error) { console.log(error);});\n            }\n        },\n        ajouter:function(){\n            this.add = true;\n        },\n        resetform :function(){\n            this.add = false;\n            this.nouveau = {\n                title: '',\n                lieux_id: '',\n                starting_at: '',\n                capacite_salle: '',\n                colloque_id: this.colloque,\n                prices:[]\n            };\n        },\n        save : function(occurence){\n\n            var occurrence = this.list[occurence.id];\n\n            var self = this;\n            axios.post('/vue/occurrence/' + occurrence.id, { occurrence, '_method' : 'put'  }).then(function (response) {\n                self.updateOccurrences(response.data.occurrences);\n            }).catch(function (error) { console.log(error);});\n\n        },\n        deleteOccurrence :function(occurence){\n\n            var model = this.list[occurence.id];\n\n            var self = this;\n            axios.post('/vue/occurrence/' + model.id, { '_method' : 'DELETE' }).then(function (response) {\n                self.updateOccurrences(response.data.occurrences);\n            }).catch(function (error) { console.log(error);});\n\n        },\n    }\n}\n</script>"]}]);


