exports.push([module.id, "\n.dragArea {\n    height: 300px;\n    margin: 0 0 20px 0;\n    padding: 3px;\n    overflow: scroll;\n    border: 1px solid #ccc;\n    border-radius: 3px;\n    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset;\n    transition: border 0.2s linear 0s, box-shadow 0.2s linear 0s;\n}\n.dragArea div {\n    width: 100%;\n    height: auto;\n    line-height: 18px;\n    padding: 5px;\n    cursor: pointer;\n    box-shadow: 0px 0px 2px 0px rgba(222, 222, 222, 1.0);\n}\n.sortable-ghost {\n\tcolor: #EAEAEA;\n\tbackground-color: #EAEAEA;\n\tborder: 1px dashed #aaa;\n}\n.sortable-chosen:not(.sortable-ghost) {\n\tcolor: #224466;\n\tbackground-color: #2299ff;\n}\n.sortable-drag {\n\tcolor: #449922;\n\tbackground-color: #44ff33;\n}\n", "", {"version":3,"sources":["BuildNewsletterModels.vue?11d290e4"],"names":[],"mappings":";AAsHA;IACA,cAAA;IACA,mBAAA;IACA,aAAA;IACA,iBAAA;IACA,uBAAA;IACA,mBAAA;IACA,iDAAA;IACA,6DAAA;CACA;AAEA;IACA,YAAA;IACA,aAAA;IACA,kBAAA;IACA,aAAA;IACA,gBAAA;IACA,qDAAA;CACA;AAEA;CACA,eAAA;CACA,0BAAA;CACA,wBAAA;CACA;AACA;CACA,eAAA;CACA,0BAAA;CACA;AACA;CACA,eAAA;CACA,0BAAA;CACA","file":"BuildNewsletterModels.vue","sourcesContent":["<template>\n    <div>\n        <div class=\"row\">\n            <div class=\"col-md-7\" id=\"StyleNewsletterCreate\">\n\n                <div class=\"btn-group pull-right\" v-if=\"mode == 'edit'\" style=\"margin-bottom:5px;\">\n                    <form method=\"post\" :action=\"action\" v-if=\"model && !isEdit\">\n                        <input name=\"_token\" :value=\"_token\" type=\"hidden\">\n                        <input type=\"hidden\" name=\"_method\" value=\"DELETE\">\n                        <input type=\"hidden\" name=\"id\" :value=\"content.id\" />\n                        <input type=\"hidden\" :value=\"campagne.id\" name=\"campagne_id\">\n                        <button v-if=\"model && !isEdit\" @click=\"editMode(model)\" type=\"button\" class=\"btn btn-xs btn-warning\">éditer</button>\n                        <button type=\"submit\" class=\"btn btn-xs btn-danger deleteNewsAction\" :data-id=\"content.id\" :data-action=\"model.title\">x</button>\n                    </form>\n                </div>\n\n                <analyse-newsletter\n                        v-if=\"model && type == 5 && newsletter.display == 'top'\"\n                        :title=\"newsletter.comment_title\"\n                        :arret=\"model\"\n                        :analyses=\"model.analyses\"></analyse-newsletter>\n\n                <!-- Bloc content-->\n                <table border=\"0\" width=\"560\" align=\"center\" cellpadding=\"0\" cellspacing=\"0\" class=\"resetTable\">\n                    <tr v-if=\"model\">\n                        <td valign=\"top\" width=\"375\" class=\"resetMarge contentForm\">\n                            <h3>{{ model.dumois ? 'Arrêt du mois : ' : '' }}{{ model.title }}</h3>\n                            <p class=\"abstract\">{{ model.abstract }}</p>\n                            <div v-html=\"model.content\" class=\"content\"></div>\n                            <p><a target=\"_blank\" :class=\"model.class\" :href=\"model.link\">{{ model.message }}</a></p>\n                        </td>\n\n                        <!-- Bloc image droite-->\n                        <td width=\"25\" class=\"resetMarge\"></td><!-- space -->\n                        <td valign=\"top\" align=\"center\" width=\"160\" class=\"resetMarge\">\n                            <div v-for=\"image in model.images\">\n                                <a target=\"_blank\" :href=\"image.link\">\n                                    <img width=\"130\" border=\"0\" :alt=\"image.title\" :src=\"image.image\">\n                                </a>\n                                <p v-if=\"!newsletter.hide_title\" style=\"text-align:center !important;\">{{ image.title }}</p>\n                            </div>\n                        </td>\n                    </tr>\n                </table>\n                <!-- Bloc content-->\n\n                <analyse-newsletter\n                        v-if=\"model && type == 5 && newsletter.display == 'bottom'\"\n                        :title=\"newsletter.comment_title\"\n                        :arret=\"model\"\n                        :analyses=\"model.analyses\"></analyse-newsletter>\n\n            </div>\n\n            <div class=\"col-md-5\" v-show=\"isEdit || mode == 'create'\">\n                <form name=\"blocForm newsletterForm\" class=\"form-horizontal\" method=\"post\" :action=\"action\">\n\n                    <input name=\"_token\" :value=\"_token\" type=\"hidden\">\n                    <input v-if=\"mode == 'edit'\" type=\"hidden\" name=\"_method\" value=\"PUT\">\n                    <div class=\"panel panel-success\">\n                        <div class=\"panel-body\">\n                            <h3>{{ title }}</h3>\n\n                            <div v-if=\"type == 7\">\n                                <select class=\"form-control form-required required\" v-model=\"categorie\" name=\"id\" v-on:change=\"updateModel\">\n                                    <option v-if=\"!categorie\" :value=\"null\" disabled>Sélectionner catégorie</option>\n                                    <option v-for=\"categorie in categories\" v-bind:value=\"categorie\">{{ categorie.title }}</option>\n                                </select><br/>\n\n                                <div class=\"row drag\">\n                                    <div class=\"col-md-6\">\n                                        <draggable v-model=\"arrets\" class=\"dragArea\" :options=\"{group:'arret'}\">\n                                            <div v-for=\"element in arrets\" :key=\"element.id\">{{ element.reference }}</div>\n                                        </draggable>\n                                    </div>\n                                    <div class=\"col-md-6\">\n                                        <draggable v-model=\"choosen\" class=\"dragArea\" :options=\"{group:'arret'}\">\n                                            <div v-for=\"element in choosen\" :key=\"element.id\">{{ element.reference }}</div>\n                                        </draggable>\n                                    </div>\n                                </div>\n                            </div>\n                            <div v-if=\"type != 7\">\n                                <select class=\"form-control form-required required\" v-model=\"model\" name=\"id\" v-on:change=\"updateModel\">\n                                    <option v-if=\"!model\" :value=\"null\" disabled>Sélectionner</option>\n                                    <option v-for=\"model in models\" v-bind:value=\"model\">{{ model.title }}</option>\n                                </select><br/>\n                            </div>\n\n                            <div class=\"btn-group\">\n                                <input type=\"hidden\" :value=\"type\" name=\"type_id\">\n                                <input type=\"hidden\" :value=\"campagne.id\" name=\"campagne\">\n                                <input v-if=\"type == 7 && categorie\" type=\"hidden\" :value=\"categorie.id\" name=\"categorie_id\">\n\n                                <div v-if=\"model\">\n                                    <input v-if=\"type == 5\" type=\"hidden\" :value=\"model.id\" name=\"arret_id\">\n                                    <input v-if=\"type == 8\" type=\"hidden\" :value=\"model.id\" name=\"product_id\">\n                                    <input v-if=\"type == 9\" type=\"hidden\" :value=\"model.id\" name=\"colloque_id\">\n                                </div>\n\n                                <div v-if=\"type == 7\">\n                                    <input v-for=\"chose in choosen\" type=\"hidden\" name=\"arrets[]\" :value=\"chose.id\" />\n                                </div>\n\n                                <button type=\"submit\" class=\"btn btn-sm btn-success\">Envoyer</button>\n                                <button type=\"button\" @click=\"close\" class=\"btn btn-sm btn-default cancelCreate\">Annuler</button>\n                            </div>\n\n                        </div>\n                    </div>\n\n                </form>\n            </div>\n        </div>\n    </div>\n</template>\n<style>\n\n.dragArea {\n    height: 300px;\n    margin: 0 0 20px 0;\n    padding: 3px;\n    overflow: scroll;\n    border: 1px solid #ccc;\n    border-radius: 3px;\n    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset;\n    transition: border 0.2s linear 0s, box-shadow 0.2s linear 0s;\n}\n\n.dragArea div {\n    width: 100%;\n    height: auto;\n    line-height: 18px;\n    padding: 5px;\n    cursor: pointer;\n    box-shadow: 0px 0px 2px 0px rgba(222, 222, 222, 1.0);\n}\n\n.sortable-ghost {\n\tcolor: #EAEAEA;\n\tbackground-color: #EAEAEA;\n\tborder: 1px dashed #aaa;\n}\n.sortable-chosen:not(.sortable-ghost) {\n\tcolor: #224466;\n\tbackground-color: #2299ff;\n}\n.sortable-drag {\n\tcolor: #449922;\n\tbackground-color: #44ff33;\n}\n</style>\n<script>\n\n    import draggable from 'vuedraggable';\n    import AnalyseNewsletter from './partials/AnalyseNewsletter.vue';\n\n    export default{\n\n        props: ['type','campagne','_token','url','site','title','content','mode','newsletter'],\n        components: {\n            draggable,\n            'analyse-newsletter' : AnalyseNewsletter,\n        },\n        data(){\n            return{\n                model:null,\n                choosen: [],\n                categorie: null,\n                categories: [],\n                arrets: [],\n                models: [],\n                lists:[],\n                isEdit: false,\n            }\n        },\n        computed: {\n            route: function () {\n                if(this.type == 5){ return \"admin/ajax/arrets/\" + this.site; }\n                if(this.type == 8){ return \"admin/ajax/product\"; }\n                if(this.type == 9){ return \"admin/ajax/colloque\"; }\n            },\n            selected: function () {\n                if(this.type == 5){ this.content.arret }\n                if(this.type == 8){ this.content.product }\n                if(this.type == 9){ this.content.colloque }\n            },\n            action:function(){\n                if(this.mode == 'edit'){ return this.url + '/' + this.content.id; }\n                if(this.mode == 'create'){ return this.url; }\n            },\n            prepared: function () {\n                var arr = [];\n                _.each(this.choosen,function(o){\n                   arr.push(_.pick(o,'id'));\n                });\n\n                return arr;\n            }\n        },\n        watch : {\n           type:function(val) {\n              this.models = [];\n              this.getModels(this.route);\n           },\n        },\n        mounted: function ()  {\n            this.getModels(this.route);\n            this.getCategories();\n            this.initialize();\n        },\n        methods: {\n            initialize : function(){\n                this.model  = this.content ? this.content.model : null;\n                this.isEdit = !this.content ? true : false;\n            },\n            getModels: function(route) {\n                var self = this;\n                axios.get(route).then(function (response) {\n                      self.models = response.data;\n                }).catch(function (error) { console.log(error);});\n            },\n            getCategories: function() {\n                var self = this;\n                axios.get('admin/ajax/categories/' + self.site).then(function (response) {\n                      self.categories = response.data;\n                      self.lists.push(self.categories);\n                }).catch(function (error) { console.log(error);});\n            },\n            getArretsCategories: function() {\n                var self = this;\n                axios.post(\"admin/ajax/categorie/categoriearrets\",{ id: self.categorie.id }).then(function (response) {\n                      self.arrets = response.data;\n                }).catch(function (error) { console.log(error);});\n            },\n            updateModel(){\n                this.getArretsCategories();\n            },\n            editMode(model){\n                this.isEdit = true;\n            },\n            close(){\n                this.isEdit = false;\n                this.initialize();\n                if(this.mode == 'create'){\n                    this.model = null;\n                    this.$emit('cancel', this.cancel);\n                }\n            },\n            deleteContent(model){\n                //this.$emit('deleteContent', model);\n            }\n        }\n    }\n</script>\n"]}]);


