n                    <span><a target=\"_blank\" :href=\"'admin/user/' + chosen.user_id\">{{ chosen.name }}</a></span>\n                    <span v-if=\"chosen.cp\">{{ chosen.cp }}</span>\n                    <span v-if=\"chosen.complement\">{{ chosen.complement }}</span>\n                    <span>{{ chosen.adresse }}</span>\n                    <span>{{ chosen.npa }} {{ chosen.ville }}</span>\n                    <button type=\"button\" class=\"btn btn-danger btn-xs pull-right\" @click.prevent=\"remove\">changer</button>\n                </div>\n            </div>\n\n        </div>\n    </div>\n</template>\n<style>\n    .autocomplete-bloc{\n        padding:10px 0;\n        margin-top:5px;\n    }\n    .autocomplete-bloc span{\n        display:block;\n    }\n    .autocomplete-bloc .btn.btn-danger{\n        margin-top:8px;\n    }\n    .empty-text{\n        margin-top:5px;\n    }\n    .no-adresse {\n        position: absolute;\n        top: 5px;\n        right: 5px;\n        display: block;\n        color: #7b7b7b;\n        font-size: 12px;\n        border: 1px solid #d4d4d4;\n        padding: 3px;\n        width:auto;\n        text-align: center;\n    }\n</style>\n\n<script>\n    export default{\n        props: ['type','chosen_id'],\n        data(){\n            return{\n                chosen: {\n                    civilite : '',\n                    name : '',\n                    company: '',\n                    adresse: '',\n                    cp: '',\n                    npa: '',\n                    ville: '',\n                    user_id: null\n                },\n                hasChosen: false,\n                noResult: false,\n                searching: false\n            }\n        },\n        mounted: function ()  {\n\n            if(this.chosen_id){ this.fetch(); }\n\n            this.$nextTick(function() {\n\n                let self = this;\n\n                $(\".search-adresse-autocomplete_\" + this.type).keypress(function(e) {\n                    var code = (e.keyCode ? e.keyCode : e.which);\n                    if(code == 13) { //Enter keycode\n                        return false;\n                    }\n                });\n\n                $(\".search-adresse-autocomplete_\" + this.type).blur(function(){\n                     if($(this).val() === '') {\n                          self.noResult = false;\n                     }\n                 })\n\n                $(\".search-adresse-autocomplete_\" + this.type).autocomplete({\n                    source    : base_url + 'vue/autocomplete',\n                    minLength : 2,\n                    search: function( event, ui ) {\n                        self.searching = true;\n                    },\n                    select : function( event, ui ) {\n                         self.chosen = ui.item.user;\n                         self.hasChosen = true;\n                         self.searching = false;\n                         self.noResult = false;\n                         console.log(ui.item.user);\n                         return false;\n                    },\n                    response: function(event, ui) {\n                        if (ui.content.length === 0) {\n                            self.searching = false;\n                            self.noResult = true;\n                        }\n                    },\n                    change: function(event, ui) {\n                        console.log($(this).val());\n                         self.searching = false;\n                        if($(this).val() === '') {\n                             self.noResult = false;\n                        }\n                    }\n                }).autocomplete( \"instance\" )._renderItem = function( ul, item ){\n                    return $(\"<li>\").append(\"<a>\" + item.label + \"<span>\" + item.desc + \"</span><span>\" + item.company + \"</span></a>\").appendTo(ul);\n                };\n\n            });\n        },\n        methods: {\n            remove () {\n                this.hasChosen = false;\n                this.chosen = {\n                    civilite : '',\n                    name : '',\n                    company: '',\n                    adresse: '',\n                    cp: '',\n                    npa: '',\n                    ville: '',\n                    user_id: null\n                };\n                this.user_id = null;\n            },\n            updateOptions(options){\n                this.options = options;\n            },\n            fetch () {\n\n                var self = this;\n                axios.get('admin/user/getUser/' + this.chosen_id, {}).then(function (response) {\n                    console.log(response.data);\n                    self.chosen = response.data;\n                    self.hasChosen = true;\n                    self.noResult = false;\n                }).catch(function (error) { console.log(error);});\n            },\n        }\n    }\n</script>\n"]}]);


